  .header-title {
    display: block;
    float: left;
    margin: 1.5rem 1rem;
    font-size: 1.25rem;
  }

  .header-menu {
    position: absolute;
    right: 50px;
    top: 5px;
  }

  .header-menu > button > span.MuiButton-label {
    color: white;
  }

}

.app-general-dialog {
  .title-icon {
    float: right;
  }
}

.login-dialog {
  .title-icon {
    float: right;
  }
}

.configuration-dialog {
  margin: 20px;

  .top-margin {
    margin-top: 20px;
  }

  .message {
    font-weight: bold;
    color: #63A84E;
    margin-top: 20px !important;
  }

  .message.error{
    color: #DA4148;
  }
}

.page-content {
  height: calc(100vh - 4rem);
  background-color: #ffffff;
  overflow-y: auto;

  .content-header {
    font-size: 1.25rem;
    color: #757575;
    background-color: #eeeeee;
    line-height: 2rem;
    padding: 0.75rem 1rem;
  }

  .content-text {
    font-size: 1rem;
    color: #757575;
    background-color: #eeeeee;
    line-height: 2rem;
    padding: 0.75rem 1rem;
  }

  .MuiTableRow-head {
    background-color: #e1e1e1;
    .MuiTableCell-head {
      font-weight: bold;
    }
    .MuiTableCell-stickyHeader {
      background-color: #e1e1e1;
      font-weight: bold;
    }
  }


  .tab-caption {
    background-color: #e2e2e2;
  }
}

.dialog-content {
  
  .content-header {
    font-size: 1.25rem;
    color: #757575;
    background-color: #eeeeee;
    line-height: 2rem;
    padding: 0.75rem 1rem;
  }

  .content-text {
    font-size: 1rem;
    color: #757575;
    background-color: #eeeeee;
    line-height: 2rem;
    padding: 0.75rem 1rem;
  }

  .MuiTableRow-head {
    background-color: #e1e1e1;
    .MuiTableCell-head {
      font-weight: bold;
    }
  }

  
}
  
  
 .align-left {
  float: left;
  margin-inline-start: 5px;
 }
 .align-right {
   float: right;
   margin-inline-end: 5px;
 }
 
 
.add-document {
  .file-input {
    visibility:hidden;
    width:0px;
  }
 
  .inline {
    display: inline;
  }

  .margin-start {
    margin-inline-start: 5px;
  }
}
 